import React from 'react'
import Me from '../../ashraf.png'
import './Footer.css'
import { animateScroll as scroll } from 'react-scroll/modules'

const Footer = () => {
    return (
        <div>
            <footer className="footer-area">
                <div className="container">
                    <div className="fo">
                        <div className="text-center py-2">
                            <a href="#"><img src={Me} className='fo-img' alt="logo"/></a>
                        </div>
                        <div className="social text-center">
                            <h5 className="text-uppercase py-">Follow me</h5>
                            <a href="#"><i className="fab fa-facebook"></i></a>
                            <a href="#"><i className="fab fa-instagram"></i></a>
                            <a href="#"><i className="fab fa-youtube"></i></a>
                            <a href="#"><i className="fab fa-twitter"></i></a>
                        </div>
                        <div className="copyrights text-center">
                            <p className="para">
                                Copyright ©2019 All rights reserved |
                                <a href="#"><span className='px-2'>Ashraf Ansari</span></a>
                            </p>

                        </div>
                    </div>
                    <div className="text-center up">
                        <i className="fas fa-arrow-circle-up fa-2x" onClick={() => scroll.scrollToTop()}></i>
                    </div>
                </div>
            </footer>
        </div>
    )
}

export default Footer
