import React from 'react'
import ashraf from '../ashraf.png'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faHamburger } from '@fortawesome/free-solid-svg-icons'
import {Link, animateScroll as scroll} from 'react-scroll'


const Navbar = () => {
    return (
        <nav className="navbar navbar-expand-lg navbar-light bg-dark">
            <div className="container">
                <a className="navbar-brand" href="#"><img className='logo' src={ashraf} alt="my-logo" /></a>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    {/* <span className="navbar-toggler-icon"></span> */}
                    <FontAwesomeIcon icon={faHamburger} />
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
                        <li className="nav-item">
                            <a className="nav-link active" aria-current="page" href="#">Home</a>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="about-me" smooth={true} duration={2000}>About me</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="services" smooth={true} duration={2000}>Services</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="experience" smooth={true} duration={2000}>Experience</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="portfolio" smooth={true} duration={2000}>Portfolio</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="contact-me" smooth={true} duration={2000}>Contact me</Link>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>
    )
}

export default Navbar
