import React from 'react'
import './MyIntro.css'
import AboutUs from '../../about-us.png'
import MyCV from '../../my-cv.pdf'

const MyIntro = () => {
    return (
        <div className="container py-2" id='about-me'>
             <section className="about-area">
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-lg-6 col-md-12">
                            <div className="about-image">
                                <img src={AboutUs} alt="About us" className="img-fluid"/>
                            </div>
                        </div>
                        <div className="col-lg-6 col-md-12 about-title">
                            <h2 className="text-uppercase pt-5">
                                Let me introduce myself
                            </h2>
                            <div className="paragraph py-4">
                                <p className="para">
                                    Lorem ipsum dolor sit amet consectetur adipisicing elit. Error rerum iure obcaecati vel
                                    possimus officia maiores perferendis ut! Quos, perspiciatis.
                                </p>
                                <p className="para">
                                    It is a long established fact that a reader will be distracted by the readable content
                                    of a page when looking at its layout. The point of using Lorem Ipsum is that it has a
                                    more-or-less normal distribution of letters, as opposed to using 'Content here, content
                                    here
                                </p>
                            </div>
                            <a href={MyCV} target="_blank" type="button" className="btn button primary-button text-uppercase btn-dark">My CV</a>
                        </div>
                    </div>
                </div>
            </section>
        </div>
    )
}

export default MyIntro
