import React, {useState, useEffect } from 'react'
import './ContactTwo.css'
import envelope from '../envelope.png'
import {db} from '../firebase'


const Contact = () => {

    const [fullName, setFullName] = useState('')
    const [email, setEmail] = useState('')
    const [phone, setPhone] = useState('')
    const [message, setMessage] = useState('')
    const [result, setResult] = useState(false)

    const handleSubmit = (e) => {
        e.preventDefault();
        db.collection('contacts').add({
            fullName: fullName, 
            email: email,
            phone: phone,
            message: message
        })
        .then(res => {
            console.log('');
        })
        .catch(err => {
            console.log(err);
        })

        setFullName('')
        setEmail('')
        setPhone('')
        setMessage('')
        setResult(true)
        setTimeout( () => {
            setResult(false)
        }, 5000)
    }

    return (

        <div className='container' id='contact-me'>
            <div className="contact1">
                <div className="container-contact1">
                    <div className="contact1-pic js-tilt" data-tilt>
                        <img src={envelope} alt="IMG"/>
                    </div>

                        <form className="contact1-form validate-form" onSubmit={handleSubmit}>
                            <span className="contact1-form-title">
                                Get in touch
                            </span>

                            <div className="wrap-input1 validate-input" data-validate = "Name is required">
                                <input 
                                    className="input1" 
                                    type="text" 
                                    placeholder="Name"
                                    name="fullName" 
                                    required
                                    value={fullName}
                                    onChange={(e) => setFullName(e.target.value)}
                                    placeholder="Enter your Full name"/>
                                <span className="shadow-input1"></span>
                            </div>

                            <div className="wrap-input1 validate-input" data-validate = "Valid email is required: ex@abc.xyz">
                                <input 
                                    className="input1" 
                                    required
                                    type="text" 
                                    placeholder="Email"
                                    name="email"
                                    value={email}
                                    onChange={(e) => setEmail(e.target.value)}
                                    />
                                <span className="shadow-input1"></span>
                            </div>

                            <div className="wrap-input1 validate-input" data-validate = "Subject is required">
                                <input 
                                    className="input1" 
                                    type="tel" 
                                    placeholder="Enter your phone number"
                                    aria-required
                                    name="phone"
                                    value={phone}
                                    onChange={(e) => setPhone(e.target.value)}
                                    />
                                <span className="shadow-input1"></span>
                            </div>

                            <div className="wrap-input1 validate-input" data-validate = "Message is required">
                                <textarea 
                                    className="input1" 
                                    name="message" 
                                    required
                                    placeholder="Message"
                                    value={message}
                                    onChange={(e) => setMessage(e.target.value)}
                                    ></textarea>
                                <span className="shadow-input1"></span>
                            </div>

                            <div className="container-contact1-form-btn">
                                <button className="contact1-form-btn">
                                    <span>
                                        Send Email
                                        <i className="fa fa-long-arrow-right" aria-hidden="true"></i>
                                    </span>
                                </button>
                                <p>{result ? 'Your message has been sent' : ''}</p>
                            </div>
                        </form>
                    </div>
                </div>
        </div>
    )
}

export default Contact
